import React from 'react'
import {useState,useEffect } from 'react'
import  { Link } from 'react-router-dom'
import { Button } from './Button'

function Navbar() {
    const [click, setclick]=useState(false)
    const [button, setButton] = useState(true);
    const handleclick =()=>setclick(!click)
    const closemenu=()=>setclick(false)
    const showButton = () => {
        if (window.innerWidth <= 960) {
          setButton(false);
        } else {
          setButton(true);
        }
      };
    
      useEffect(() => {
        showButton();
      }, []);
    
      window.addEventListener('resize', showButton);
    return (
        <nav className="navbar">
           <div className="navbar-container">
            <Link to='/' className='navbar-logo'>
                TRVL <i className='fab fa-typo3' />
            </Link>
            <div className='menu-icon' onClick={handleclick}>
                <i className={click ? 'fas fa-times' : 'fas fa-bars '}/>
            </div>
            <ul className={click ? 'nav-menu active ':'nav-menu '}>
                <li className='nav-item'>
                    <Link to='/' className='nav-links' onClick={closemenu}>
                        HOME
                    </Link>
                </li>
                <li className='nav-item'>
                    <Link to='/Services' className='nav-links' onClick={closemenu}>
                        SERVICE
                    </Link>
                </li>
                <li className='nav-item'>
                    <Link to='/Products' className='nav-links' onClick={closemenu}>
                        PRODUCT
                    </Link>
                </li>
                <li className='nav-item'>
                    <Link to='/sign-up' className='nav-links' onClick={closemenu}>
                        SIGN UP
                    </Link>
                </li>
            </ul>
            {button && <Button buttonStyle='btn-outline'></Button>}

           </div>
            
        </nav>
    )
}

export default Navbar
